import * as mutations from "../mutation-types";
import * as api from "../../services/API";

export default {
  namespaced: true,
  state: {
    certificate: {
      title: null
    },
    certificates: [],
    certificate_created: false,
    certificate_deleted: false,
    headers: [
      { text: "#ID", value: "data.attributes.id" },
      { text: "Title", value: "data.attributes.title" },
      { text: "Actions", value: "action", sortable: false }
    ],
    loading: false,
    currentPage: 1,
    total_certificates: 0,
    number_of_pages: 0
  },
  getters: {
    getCertificate: function(state) {
      return state.certificate;
    },
    getCertificates: function(state) {
      return state.certificates;
    },
    getCertificateCreated: function(state) {
      return state.certificate_created;
    },
    getCertificateDeleted: function(state) {
      return state.certificate_deleted;
    },
    getLoadingStatus: function(state) {
      return state.loading;
    },
    getTableHeader: function(state) {
      return state.headers;
    },
    getCurrentPage: function(state) {
      return state.currentPage;
    },
    getTotalCertificates: function(state) {
      return state.total_certificates;
    },
    getNumberOfPages: function(state) {
      return state.number_of_pages;
    }
  },
  mutations: {
    updateTitle(state, title) {
      state.certificate.title = title;
    },
    [mutations.SET_CERTIFICATE](state, certificate) {
      state.certificate = certificate;
    },
    [mutations.SET_CERTIFICATES](state, certificates) {
      state.certificates = certificates;
    },
    [mutations.SET_NEW_CERTIFICATE](state, certificate) {
      state.certificates.unshift(certificate);
    },
    [mutations.SET_UPDATED_CERTIFICATE](state, updatedCertificate) {
      Object.assign(
        state.certificates.find(
          element =>
            element.data.attributes.id === updatedCertificate.data.attributes.id
        ),
        updatedCertificate
      );
    },
    [mutations.SET_CERTIFICATE_CREATED](state, result) {
      state.certificate_created = result;
    },
    [mutations.SET_REMOVE_CERTIFICATE](state, id) {
      state.certificates = state.certificates.filter(
        certificate => certificate.data.attributes.id !== id
      );
    },
    [mutations.SET_CERTIFICATE_DELETED](state, result) {
      state.certificate_deleted = result;
    },
    [mutations.SET_LOADING_CERTIFICATE](state, result) {
      state.loading = result;
    },
    [mutations.SET_CERTIFICATE_CURRENT_PAGE](state, currentPage) {
      state.currentPage = currentPage;
    },
    [mutations.SET_TOTAL_CERTIFICATES](state, totalCertificates) {
      state.total_certificates = totalCertificates;
    },
    [mutations.SET_CERTIFICATE_NUMBER_OF_PAGES](state, numberOfPages) {
      state.number_of_pages = numberOfPages;
    }
  },
  actions: {
    async searchCertificates({ commit }, payload) {
      let sortBy = "";
      let sortDesc = "";
      if (payload.sortBy.length == 1) {
        let sortByQuery = payload.sortBy[0].split(".");
        sortBy = sortByQuery[2];
      }

      if (payload.sortDesc.length == 1) {
        sortDesc = payload.sortDesc[0];
      }

      commit(mutations.SET_LOADING_CERTIFICATE, true);
      let response = await api.publicAPI.get(
        `certificates/?page=${payload.page}&per-page=${payload.itemsPerPage}&sort-by=${sortBy}&sort-desc=${sortDesc}`
      );
      commit(mutations.SET_CERTIFICATES, response.data.data);
      commit(mutations.SET_LOADING_CERTIFICATE, false);
      commit(
        mutations.SET_CERTIFICATE_CURRENT_PAGE,
        response.data.meta.current_page
      );
      commit(mutations.SET_TOTAL_CERTIFICATES, response.data.meta.total);
      commit(
        mutations.SET_CERTIFICATE_NUMBER_OF_PAGES,
        response.data.meta.last_page
      );
    },
    async fetchCertificate({ commit }, certificateId) {
      let response = await api.publicAPI.get(`certificates/${certificateId}`);
      commit(mutations.SET_CERTIFICATE, response.data.data.attributes);
    },
    async addCertificate({ state, commit }) {
      let response = null;
      if (state.certificate.id) {
        response = await api.publicAPI.put(
          `certificates/${state.certificate.id}`,
          state.certificate
        );
        commit(mutations.SET_UPDATED_CERTIFICATE, response.data);
      } else {
        response = await api.publicAPI.post(`certificates/`, state.certificate);
        commit(mutations.SET_NEW_CERTIFICATE, response.data);
      }
      commit(mutations.SET_CERTIFICATE_CREATED, true);
    },
    async deleteCertificate({ commit }, id) {
      commit(mutations.SET_CERTIFICATE_DELETED, false);
      await api.publicAPI.delete(`certificates/${id}`);
      commit(mutations.SET_REMOVE_CERTIFICATE, id);
      commit(mutations.SET_CERTIFICATE_DELETED, true);
    }
  }
};
