import * as mutations from "../mutation-types";
import * as api from "../../services/API";

export default {
  namespaced: true,

  state: {
    student: {
      first_name: null,
      last_name: null,
      date_of_birth: new Date().toISOString().substr(0, 10),
      nic: null,
      passport: null
    },
    students: [],
    student_created: false,
    student_deleted: false,
    headers: [
      { text: "#ID", value: "data.attributes.id" },
      { text: "First Name", value: "data.attributes.first_name" },
      { text: "Last Name", value: "data.attributes.last_name" },
      {
        text: "Date Of Birth",
        value: "data.attributes.date_of_birth",
        sortable: false
      },
      { text: "NIC & Passport", value: "nic", sortable: false },
      {
        text: "Created On",
        value: "data.attributes.created_at",
        sortable: false
      },
      { text: "Actions", value: "action", sortable: false }
    ],
    loading: false,
    currentPage: 1,
    total_students: 0,
    number_of_pages: 0
  },

  getters: {
    getStudent: function(state) {
      return state.student;
    },
    getAllStudents: function(state) {
      return state.students;
    },
    getStudentCreated: function(state) {
      return state.student_created;
    },
    getStudentDeleted: function(state) {
      return state.student_deleted;
    },
    getLoadingStatus: function(state) {
      return state.loading;
    },
    getTableHeader: function(state) {
      return state.headers;
    },
    getCurrentPage: function(state) {
      return state.currentPage;
    },
    getTotalStudents: function(state) {
      return state.total_students;
    },
    getNumberOfPages: function(state) {
      return state.number_of_pages;
    }
  },

  mutations: {
    updateFirstName(state, firstName) {
      state.student.first_name = firstName;
    },
    updateLastName(state, lastName) {
      state.student.last_name = lastName;
    },
    updateDateOfBirth(state, dateOfBirth) {
      state.student.date_of_birth = dateOfBirth;
    },
    updateNic(state, nic) {
      state.student.nic = nic;
    },
    updatePassport(state, passport) {
      state.student.passport = passport;
    },
    [mutations.SET_STUDENT](state, student) {
      state.student = student;
    },
    [mutations.SET_STUDENTS](state, students) {
      state.students = students;
    },
    [mutations.SET_NEW_STUDENT](state, student) {
      state.students.unshift(student);
    },
    [mutations.SET_UPDATED_STUDENT](state, updatedStudent) {
      Object.assign(
        state.students.find(
          element =>
            element.data.attributes.id === updatedStudent.data.attributes.id
        ),
        updatedStudent
      );
    },
    [mutations.SET_STUDENT_CREATED](state, result) {
      state.student_created = result;
    },
    [mutations.SET_REMOVE_STUDENT](state, id) {
      state.students = state.students.filter(
        student => student.data.attributes.id !== id
      );
    },
    [mutations.SET_STUDENT_DELETED](state, result) {
      state.student_deleted = result;
    },
    [mutations.SET_LOADING_STUDENT](state, result) {
      state.loading = result;
    },
    [mutations.SET_CURRENT_PAGE](state, currentPage) {
      state.currentPage = currentPage;
    },
    [mutations.SET_TOTAL_STUDENTS](state, totalStudents) {
      state.total_students = totalStudents;
    },
    [mutations.SET_NUMBER_OF_PAGES](state, numberOfPages) {
      state.number_of_pages = numberOfPages;
    }
  },

  actions: {
    async searchStudents({ commit }, payload) {
      let sortBy = "";
      let sortDesc = "";
      if (payload.sortBy.length == 1) {
        let sortByQuery = payload.sortBy[0].split(".");
        sortBy = sortByQuery[2];
      }

      if (payload.sortDesc.length == 1) {
        sortDesc = payload.sortDesc[0];
      }

      commit("SET_LOADING_STUDENT", true);
      let response = await api.publicAPI.get(
        `students/?page=${payload.page}&per-page=${payload.itemsPerPage}&sort-by=${sortBy}&sort-desc=${sortDesc}`
      );
      commit("SET_STUDENTS", response.data.data);
      commit("SET_LOADING_STUDENT", false);
      commit("SET_CURRENT_PAGE", response.data.meta.current_page);
      commit("SET_TOTAL_STUDENTS", response.data.meta.total);
      commit("SET_NUMBER_OF_PAGES", response.data.meta.last_page);
    },
    async fetchStudent({ commit }, studentId) {
      let response = await api.publicAPI.get(`students/${studentId}`);
      commit("SET_STUDENT", response.data.data.attributes);
    },
    async addStudent({ state, commit }) {
      let response = null;
      if (state.student.id) {
        response = await api.publicAPI.put(
          `students/${state.student.id}`,
          state.student
        );
        commit(mutations.SET_UPDATED_STUDENT, response.data);
      } else {
        response = await api.publicAPI.post(`students/`, state.student);
        commit(mutations.SET_NEW_STUDENT, response.data);
      }
      commit(mutations.SET_STUDENT_CREATED, true);
    },
    async deleteStudent({ commit }, id) {
      commit(mutations.SET_STUDENT_DELETED, false);
      await api.publicAPI.delete(`students/${id}`);
      commit(mutations.SET_REMOVE_STUDENT, id);
      commit(mutations.SET_STUDENT_DELETED, true);
    },
    async fetchStudentByNicOrPassport({ commit }, nicOrPassport) {
      let response = await api.publicAPI.get(
        `students/find-by-nic-passport/${nicOrPassport}`
      );
      commit("SET_STUDENT", response.data.data.attributes);
    }
  }
};
