import * as mutations from "../mutation-types";
import * as api from "@/services/API";

export default {
  namespaced: true,
  state: {
    award: {
      id: null,
      certificate_id: null,
      registration_number: null,
      certificate_number: null,
      student_id: null,
      from_date: null,
      to_date: null,
      grade: null
    },
    student: {
      nic: null,
      passport: null
    },
    awards: [],
    award_created: false,
    award_deleted: false,

    headers: [
      { text: "#ID", value: "data.attributes.id" },
      { text: "Cert Number", value: "data.attributes.certificate_number" },
      { text: "Reg Number", value: "data.attributes.registration_number" },
      {
        text: "Student",
        value: "data.attributes.student.first_name",
        sortable: false
      },
      {
        text: "NIC / Passport",
        value: "data.attributes.student.nic",
        sortable: false
      },
      {
        text: "From",
        value: "data.attributes.from_date",
        sortable: false
      },
      {
        text: "To",
        value: "data.attributes.to_date",
        sortable: false
      },
      { text: "Grade", value: "data.attributes.grade", sortable: false },
      {
        text: "Created On",
        value: "data.attributes.created_at",
        sortable: false
      },
      { text: "Actions", value: "action", sortable: false }
    ],
    loading: false,
    currentPage: 1,
    total_awards: 0,
    number_of_pages: 0
  },

  getters: {
    getAward: function(state) {
      return state.award;
    },
    getAwards: function(state) {
      return state.awards;
    },
    getAwardCreated: function(state) {
      return state.award_created;
    },
    getAwardDeleted: function(state) {
      return state.award_deleted;
    },
    getLoadingStatus: function(state) {
      return state.loading;
    },
    getTableHeader: function(state) {
      return state.headers;
    },
    getCurrentPage: function(state) {
      return state.currentPage;
    },
    getTotalAwards: function(state) {
      return state.total_awards;
    },
    getNumberOfPages: function(state) {
      return state.number_of_pages;
    }
  },
  mutations: {
    updateRegistrationNumber(state, registration_number) {
      state.award.registration_number = registration_number;
    },
    updateFromDate(state, from_date) {
      state.award.from_date = from_date;
    },
    updateToDate(state, to_date) {
      state.award.to_date = to_date;
    },
    updateGrade(state, grade) {
      state.award.grade = grade;
    },
    updateCertificateNumber(state, certificateNumber) {
      state.award.certificate_number = certificateNumber;
    },
    updateStudentId(state, studentId) {
      state.award.student_id = studentId;
    },
    updateCertificateId(state, certificateId) {
      state.award.certificate_id = certificateId;
    },
    [mutations.SET_AWARD](state, award) {
      state.award = award;
      state.award.certificate_id = award.certificate.id;
      state.award.student_id = award.student.id;
    },
    [mutations.SET_AWARDS](state, awards) {
      state.awards = awards;
    },
    [mutations.SET_NEW_AWARD](state, award) {
      state.awards.unshift(award);
    },
    [mutations.SET_UPDATED_AWARD](state, updatedAward) {
      Object.assign(
        state.awards.find(
          element =>
            element.data.attributes.id === updatedAward.data.attributes.id
        ),
        updatedAward
      );
    },
    [mutations.SET_AWARD_CREATED](state, result) {
      state.award_created = result;
    },
    [mutations.SET_REMOVE_AWARD](state, id) {
      state.awards = state.awards.filter(
        award => award.data.attributes.id !== id
      );
    },
    [mutations.SET_AWARD_DELETED](state, result) {
      state.award_deleted = result;
    },
    [mutations.SET_LOADING_AWARD](state, result) {
      state.loading = result;
    },
    [mutations.SET_AWARD_CURRENT_PAGE](state, currentPage) {
      state.currentPage = currentPage;
    },
    [mutations.SET_TOTAL_AWARDS](state, totalAwards) {
      state.total_awards = totalAwards;
    },
    [mutations.SET_AWARD_NUMBER_OF_PAGES](state, numberOfPages) {
      state.number_of_pages = numberOfPages;
    }
  },
  actions: {
    async searchAwards({ commit }, payload) {
      let sortBy = "";
      let sortDesc = "";
      if (payload.sortBy.length == 1) {
        let sortByQuery = payload.sortBy[0].split(".");
        sortBy = sortByQuery[2];
      }

      if (payload.sortDesc.length == 1) {
        sortDesc = payload.sortDesc[0];
      }
      commit(mutations.SET_LOADING_AWARD, true);
      let response = await api.publicAPI.get(
        `awards/?page=${payload.page}&per-page=${payload.itemsPerPage}&sort-by=${sortBy}&sort-desc=${sortDesc}`
      );
      commit(mutations.SET_AWARDS, response.data.data);
      commit(mutations.SET_LOADING_AWARD, false);
      commit(mutations.SET_AWARD_CURRENT_PAGE, response.data.meta.current_page);
      commit(mutations.SET_TOTAL_AWARDS, response.data.meta.total);
      commit(mutations.SET_AWARD_NUMBER_OF_PAGES, response.data.meta.last_page);
    },
    async fetchAward({ commit }, awardId) {
      let response = await api.publicAPI.get(`awards/${awardId}`);
      commit(mutations.SET_AWARD, response.data.data.attributes);
    },
    async addAward({ state, commit }) {
      let response = null;
      if (state.award.id) {
        response = await api.publicAPI.put(
          `awards/${state.award.id}`,
          state.award
        );
        commit(mutations.SET_UPDATED_AWARD, response.data);
      } else {
        response = await api.publicAPI.post(`awards/`, state.award);
        commit(mutations.SET_NEW_AWARD, response.data);
      }
      commit(mutations.SET_AWARD_CREATED, true);
    },
    async deleteAward({ commit }, id) {
      commit(mutations.SET_AWARD_DELETED, false);
      await api.publicAPI.delete(`awards/${id}`);
      commit(mutations.SET_REMOVE_AWARD, id);
      commit(mutations.SET_AWARD_DELETED, true);
    }
  }
};
