import * as mutations from "../mutation-types";
import * as api from "../../services/API";

export default {
  namespaced: true,
  state: {
    user: {
      email: null,
      password: null,
      device_name: null
    },
    currentUser: {
      id: null,
      name: null,
      email: null
    },
    token: localStorage.getItem("token")
  },
  getters: {
    isAuthenticated: state => !!state.token,
    getCurrentUser: function() {
      return localStorage.getItem("currentUser");
    }
  },
  mutations: {
    updateEmail(state, email) {
      state.user.email = email;
    },
    updatePassword(state, password) {
      state.user.password = password;
    },
    updateDeviceName(state, deviceName) {
      state.user.device_name = deviceName;
    },
    [mutations.SET_USER_LOGGED_IN](state, loggedIn) {
      state.loggedIn = loggedIn;
    },
    [mutations.SET_USER_TOKEN](state, token) {
      localStorage.setItem("token", token);
      state.token = token;
    },
    [mutations.SET_CURRENT_USER](state, user) {
      localStorage.setItem("currentUser", JSON.stringify(user));
      state.currentUser = user;
    }
  },
  actions: {
    async login({ state, commit }) {
      let response = await api.publicAPI.post(`login`, state.user);
      if (
        response.data.data.user != null &&
        response.data.data.user != undefined
      ) {
        commit(mutations.SET_USER_TOKEN, response.data.data.token);
        commit(mutations.SET_CURRENT_USER, response.data.data.user);
      }
    },
    async fetchUser({ commit }) {
      let response = await api.privateAPI.get(`auth/show`);
      commit(mutations.SET_CURRENT_USER, response.data.data.attributes);
    },
    async signOut() {
      localStorage.removeItem("token");
      localStorage.removeItem("currentUser");
    }
  }
};
