<template>
  <router-view />
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "App",

  data: () => ({
    loading: false
  }),
  computed: {
    ...mapGetters("auth", ["getCurrentUser"])
  },
  methods: {
    ...mapActions("auth", ["fetchUser"]),
    initialize: function() {
      this.fetchUser()
        .then(() => {
          let currentUser = this.getCurrentUser;
          if (currentUser.id === null) {
            this.$router.push({ path: "/login" });
          }
        })
        .catch(() => {
          this.$router.push({ path: "/login" });
        });
    }
  },
  async beforeMount() {
    //await this.initialize();
  }
};
</script>
