import Vue from "vue";
import VueRouter from "vue-router";
import PublicLayout from "@/layouts/PublicLayout";
import ProtectedLayout from "@/layouts/ProtectedLayout";
import store from "@/store";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    meta: { requiresAuth: true },
    component: ProtectedLayout,
    children: [
      {
        path: "",
        component: () => import("../views/Home")
      }
    ]
  },
  {
    path: "/students",
    name: "Student",
    meta: { requiresAuth: true },
    component: ProtectedLayout,
    children: [
      {
        path: "",
        component: () => import("../views/student/Student")
      }
    ]
  },
  {
    path: "/certificates",
    name: "Certificate",
    meta: { requiresAuth: true },
    component: ProtectedLayout,
    children: [
      {
        path: "",
        component: () => import("../views/certificate/Certificate")
      }
    ]
  },
  {
    path: "/awards",
    name: "Award",
    meta: { requiresAuth: true },
    component: ProtectedLayout,
    children: [
      {
        path: "",
        component: () => import("../views/award/Award")
      }
    ]
  },
  {
    path: "/login",
    name: "Login",
    component: PublicLayout,
    children: [
      {
        path: "",
        component: () => import("../views/auth/Login")
      }
    ]
  },
  {
    path: "/reset-password",
    name: "ResetPassword",
    component: PublicLayout,
    children: [
      {
        path: "",
        component: () => import("../views/auth/ForgotPassword")
      }
    ]
  },
  {
    path: "/reset-password/:token",
    name: "reset-password-form",
    component: PublicLayout,
    children: [
      {
        path: "",
        component: () => import("../views/auth/ResetPasswordForm")
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    let authenticated = store.getters["auth/isAuthenticated"];
    if (!authenticated) {
      next({
        path: "/login"
      });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
