<template>
  <v-app-bar app clipped-left clipped-right color="primary" dark>
    <v-toolbar-title class="align-center d-flex">
      <span class="logo-icon mt-2">
        <img
          src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
          width="120"
        />
      </span>
      <span class="logo-text ml-2">
        <h2>Certification Verification System</h2>
      </span>
    </v-toolbar-title>

    <v-spacer />
    <!---right part -->
    <v-menu bottom left transition="scale-transition">
      <template v-slot:activator="{ on }">
        <v-btn dark icon v-on="on">
          <v-icon>mdi-dots-horizontal-circle-outline</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-subheader>REPORTS</v-subheader>
        <v-list-item-group v-model="item" color="primary">
          <v-list-item v-for="(item, i) in items" :key="i" :to="item.to">
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-menu>
    <v-btn color="primary mr-2">
      <v-icon small color="white">
        mdi-account
      </v-icon>
      <span class="mr-2"
        >Welcome
        <span class="text-decoration-underline">
          {{ JSON.parse(this.getCurrentUser).name }}
        </span>
      </span>
    </v-btn>
    <v-btn color="secondary" @click="signOutHandler">
      <span class="mr-2">Sign Out</span>
      <v-icon small color="white">
        mdi-logout
      </v-icon>
    </v-btn>
  </v-app-bar>
</template>
<script>
// Utilities
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Header",

  components: {},

  props: {
    value: {
      type: Boolean,
      default: false
    }
  },

  data: () => ({
    item: 1,
    items: [
      {
        title: "Dashboard",
        icon: "mdi-view-dashboard",
        to: "/"
      },

      {
        title: "Awards",
        icon: "mdi-trophy-variant",
        to: "/awards"
      },

      {
        title: "Students",
        icon: "mdi-human-male-female",
        to: "/students"
      },

      {
        title: "Certificates",
        icon: "mdi-book-open",
        to: "/certificates"
      }
    ],
    href() {
      return undefined;
    }
  }),

  computed: {
    ...mapGetters("auth", ["getCurrentUser"])
  },

  methods: {
    ...mapActions("auth", ["signOut"]),
    signOutHandler() {
      this.signOut();
      this.$router.push({ path: "/login" });
    }
  }
};
</script>
